'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name add
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/math/strided/ops/add}
*/
import add from '@stdlib/math-strided-ops-add';
setReadOnly( ns, 'add', add );

/**
* @name addBy
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/math/strided/ops/add-by}
*/
import addBy from '@stdlib/math-strided-ops-add-by';
setReadOnly( ns, 'addBy', addBy );

/**
* @name mul
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/math/strided/ops/mul}
*/
import mul from '@stdlib/math-strided-ops-mul';
setReadOnly( ns, 'mul', mul );

/**
* @name mulBy
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/math/strided/ops/mul-by}
*/
import mulBy from '@stdlib/math-strided-ops-mul-by';
setReadOnly( ns, 'mulBy', mulBy );

/**
* @name sub
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/math/strided/ops/sub}
*/
import sub from '@stdlib/math-strided-ops-sub';
setReadOnly( ns, 'sub', sub );

/**
* @name subBy
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/math/strided/ops/sub-by}
*/
import subBy from '@stdlib/math-strided-ops-sub-by';
setReadOnly( ns, 'subBy', subBy );


// EXPORTS //

export default ns;
