'use strict';

// MODULES //

import Complex128 from '@stdlib/complex-float64';
import real from '@stdlib/complex-real';
import imag from '@stdlib/complex-imag';


// MAIN //

/**
* Subtracts two double-precision complex floating-point numbers.
*
* @param {Complex128} z1 - complex number
* @param {Complex128} z2 - complex number
* @returns {Complex128} result
*
* @example
* import Complex128 from '@stdlib/complex-float64';
* import real from '@stdlib/complex-real';
* import imag from '@stdlib/complex-imag';
*
* var z1 = new Complex128( 5.0, 3.0 );
* // returns <Complex128>
*
* var z2 = new Complex128( -2.0, 1.0 );
* // returns <Complex128>
*
* var out = csub( z1, z2 );
* // returns <Complex128>
*
* var re = real( out );
* // returns 7.0
*
* var im = imag( out );
* // returns 2.0
*/
function csub( z1, z2 ) {
	var re = real( z1 ) - real( z2 );
	var im = imag( z1 ) - imag( z2 );
	return new Complex128( re, im );
}


// EXPORTS //

export default csub;
